@import url(https://fonts.googleapis.com/earlyaccess/notosansjp.css);
body {
  font-family: "Noto Sans JP", sans-serif;
  width: 100%;
  font-weight: bold;
}

.header {
  background-color: white;
  position: fixed;
  z-index: 100;
  height: 94px;
  display: flex;
  width: 100%;
  align-items: center;
  box-shadow: 0px 9px 3px -3px rgba(0, 0, 0, 0.3);
}
.header__inner {
  margin: 0 auto;
  width: 100%;
  padding-left: 192px;
  padding-right: 193px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.header__inner-ttl {
  display: flex;
}
.header__inner-ttl h1 {
  padding-left: 20px;
  font-size: 24px;
  line-height: 35px;
  margin: auto;
}
.header__inner-ttl h1 a {
  color: black;
  text-decoration: none;
}
.header__inner-ttl img {
  width: 47px;
  height: 47px;
}
.header__inner-nav nav ul {
  display: flex;
  flex-wrap: no-wrap;
}
.header__inner-nav nav ul li {
  padding-right: 46px;
}
.header__inner-nav nav ul li a {
  color: black;
  font-size: 16px;
  line-height: 24px;
  text-decoration: none;
}
.header__inner-btn {
  width: 155px;
  height: 37px;
  background: transparent linear-gradient(90deg, #fa41cc 0%, #6020b0 100%) 0% 0% no-repeat padding-box;
  border: none;
  border-radius: 10px;
  cursor: pointer;
  outline: none;
}
.header__inner-btn a {
  text-decoration: none;
  color: #fff;
  display: block;
  font-size: 14px;
  font-weight: bold;
  line-height: 20px;
}
@media screen and (min-width: 768px) and (max-width: 1024px) {
  .header {
    position: fixed;
    z-index: 100;
  }
  .header__inner {
    width: 80%;
    padding: 0;
  }
  .header__inner h1 {
    font-size: 24px;
  }
  .header__inner img {
    width: 30px;
    height: 30px;
  }
  .header__inner-nav {
    display: none;
  }
  .header__inner-btn {
    display: none;
  }
}
@media (max-width: 767px) {
  .header {
    width: 100%;
  }
  .header__inner {
    width: 90%;
    padding: 0;
  }
  .header__inner h1 {
    font-size: 24px;
    margin-left: 0;
  }
  .header__inner img {
    width: 10%;
    height: 10%;
    padding-left: 20px;
  }
  .header__inner-nav {
    display: none;
  }
  .header__inner-btn {
    display: none;
  }
}

.burger-btn {
  display: none;
  width: 39px;
  height: 39px;
